body.modal-open {
  overflow: hidden;
}

.backdrop {
  z-index: 160;
  width: 100%;
  height: 100%;
  background-color: var(--background-mobile-menu-color);
  opacity: 1;
  transition: opacity var(--time-duration) var(--timing-function);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .modal {
  z-index: 160;
  transform: translate(-50%, -100%)scale(.7);
}

.modal {
  width: 280px;
  z-index: 6;
  max-height: 100vh;
  background-color: #fff;
  border-radius: 4px;
  padding: 48px 20px;
  position: absolute;
  top: 20px;
  left: 50%;
  overflow-y: auto;
  box-shadow: 0 4px 4px #00000040;
}

.modal-close-button {
  width: 30px;
  height: 30px;
  box-shadow: none;
  cursor: pointer;
  background-color: #fff;
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.modal-close-button:hover, .modal-close-button:focus {
  background-color: #ff6b08;
}

.modal-close-button-svg {
  width: 100%;
  height: 100%;
  fill: #000;
  background-color: #ff6b08;
}

.current__content {
  position: relative;
  overflow: hidden;
}

